import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { Link } from 'react-router-dom'
import './App.css';

const useStyles = makeStyles({
  root: {
    maxWidth: 1000,
  },
  media: {
    width:300,
    height: 200,
  },
});


export default function MediaCard() {
  const classes = useStyles();

  
  return (
    <div className='App'>
      <header className="App-header">
      <div>
    <Card className={classes.root}>
        <Link to='/AddRice'>
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image='data:image/jpeg;base64,'
          title="Contemplative Reptile"
        />
        <CardContent  >
          <Typography  gutterBottom variant="h5" component="h2">
            Rice Category
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
in this category rice food
          </Typography>
        </CardContent>
      </CardActionArea>
      </Link>
      <CardActions>

        <Button size="small" color="primary">
      </Button>
      </CardActions>
    </Card>
    <div>



    <Card className={classes.root}>
        <Link to='/AddMeat'>
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image='https://food.fnr.sndimg.com/content/dam/images/food/fullset/2018/10/4/1/BX1501_Roast-Filet-of-Beef_s4x3.jpg.rend.hgtvcom.826.620.suffix/1538677423309.jpeg'
          title="Contemplative Reptile"
        />
        <CardContent>
          <Typography  gutterBottom variant="h5" component="h2">
            Meat Category
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
in this category Meat food
          </Typography>
        </CardContent>
      </CardActionArea>
      </Link>
      <CardActions>

        <Button size="small" color="primary">
      </Button>
      </CardActions>
    </Card>
    </div>

    <div>
    <Card className={classes.root}>
        <Link to='/AddNoGluten'>
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image='https://cdn-a.william-reed.com/var/wrbm_gb_food_pharma/storage/images/publications/food-beverage-nutrition/bakeryandsnacks.com/news/ingredients/there-is-no-such-thing-as-100-gluten-free-diet/8075403-1-eng-GB/There-is-no-such-thing-as-100-gluten-free-diet_wrbm_large.jpg'
          title="Contemplative Reptile"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            No Gluten category
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
in this category no gluten food
          </Typography>
        </CardContent>
      </CardActionArea>
      </Link>
      <CardActions>

        <Button size="small" color="primary">
      </Button>
      </CardActions>
    </Card>
    </div>
    </div>
    </header>
    </div>
  );
}
